<template>
	<view class="category-box">
		<view class="category-box-left sticky">
			<view :class="{item:true,active:active==index?'active':''}" v-for="(item,index) in categorys" :key="index"
				@click="changeCate(index)">
				{{item.title}}
			</view>
		</view>
		<view class="category-box-right">
			<view class="box" v-for="(item,index) in categorys[active].childs" :key="index">
				<view class="box-t">
					<view class="title">{{item.title}}</view>
					<view class="more"></view>
				</view>
				<view class="box-c">
					<view class="service-list">
						<navigator class="item" hover-class="none" :url="'./list?id='+son.id"
							v-for="(son,index) in item.childs" :key="index">
							<image :src="son.thumb" mode="scaleToFill" class="icon">
							</image>
							<view class="name">{{son.title}}</view>
						</navigator>

					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				active: 0,
				categorys: [],
				son_categorys: [],
				pid: 0,
				cid: 0
			}
		},
		onLoad() {
			this.getCategorys()
		},
		onShow() {
			this.active = this.vuex_id
		},
		methods: {
			changeCate(index) {
				this.active = index
				this.pid = this.categorys[this.active].id
			},
			async getCategorys() {
				let res = await this.$u.get('/v2/category/index')
				this.categorys = res.data.data
				
			}
		}
	}
</script>

<style scoped lang="scss">
	.sticky {
		position: sticky;
		top: var(--window-top);
	}

	.category-box {
		display: flex;
		justify-content: space-between;
		background-color: #F5F5F7;
		min-height: calc(100vh - var(--window-top));
	}

	.category-box .category-box-left {
		width: 200rpx;
		background-color: #fff;
		display: flex;
		flex-direction: column;
		align-items: center;
		height: calc(100vh - var(--window-top));
		overflow-y: auto;
		position: fixed;

	}

	.category-box .category-box-left .item {
		width: 160rpx;
		height: 64rpx;
		text-align: center;
		line-height: 64rpx;
		color: #333;
		border-radius: 40rpx;
		font-size: 28rpx;
		margin-top: 20rpx;
	}

	.category-box .category-box-left .item.active {
		background: linear-gradient(80deg, #FF4400 0%, #FF6600 100%);
		color: #fff;
	}

	.category-box .category-box-right {
		flex: 1;
		margin: 20rpx;
		margin-left: 220rpx;
	}

	.box {
		border-radius: 20rpx;
		/* box-shadow: 0 0 20rpx rgba(0, 0, 0, 0.1); */
		margin: 0 0 20rpx 0;
		background-color: #fff;
	}

	.box .box-t {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 20rpx;
	}

	.box .box-t .title {
		font-size: 32rpx;
		font-weight: bold;
	}

	.box .box-t .more {
		font-size: 24rpx;
		color: #777
	}

	.service-list {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		margin: 0 18rpx;
	}

	.service-list .item {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-bottom: 18rpx;
	}

	.service-list .item .name {
		font-size: 28rpx;
		margin-top: 10rpx;
	}

	.service-list .item .icon {
		width: 140rpx;
		height: 140rpx;
	}
</style>
